}
.navigation__toggle-bar {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.navigation__toggle-bar::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.navigation__toggle-bar::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
